<template>
  <div>
    <headerBar :header-data="headerData" />
    <div class="container">
      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>基本信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-form>
            <van-field v-model="bidform.xmmc" :error-message="message1" required label="项目名称" label-width="100" placeholder="请输入项目名称" @blur="check(0)" @input="check(0)" />
            <div @click="show = true">
              <van-field v-model="bidform.jzsj" readonly label="询价截止时间" required label-width="100" :error-message="message2" placeholder="请选择询价截止时间" />
            </div>
            <van-field label="供应商征集方式" required label-width="100" :error-message="message3">
              <template #input>
                <van-radio-group v-model="bidform.gysxzfs" direction="horizontal" @change="check(2)">
                  <van-radio name="1">公开</van-radio>
                  <van-radio name="2">邀请</van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field v-model="bidform.lxr" type="text" label="联系人" label-width="100" placeholder="请输入联系人名称" />
            <van-field v-model="bidform.lxdh" type="text" label="联系方式" label-width="100" placeholder="请输入联系方式" />
            <van-field v-model="bidform.email" type="text" :error-message="message4" required label="邮箱" label-width="100" placeholder="请输入邮箱" @blur="check(3)" @input="check(3)" />
            <van-field v-model="bidform.bz" rows="1" autosize type="textarea" label="备注" label-width="100" placeholder="请输入备注信息" />
            <div class="page-list">
              <div class="pagelist-left">附件</div>
              <div class="pagelist-right left" style="margin-left: 0">
                <XyUploadPlus
                  ref="uploadEl"
                  style="width:100%;"
                  filed="test1"
                  :filegroup="filegroup"
                  @getFileChangeInfo="getFileChangeInfo"
                />
              </div>
            </div>
          </van-form>
        </div>
      </div>

      <div v-if="bidform.gysxzfs==='2'" class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>选择供应商</h2>
            </div>
            <div class="title-right" @click="showGysPopup()">新增供应商 <i class="iconfont iconarrowright" /></div>
          </div>
          <!-- 通用标题 end -->

          <div class="common-list">
            <van-swipe-cell v-for="(item,index) of xzGysList" :key="index">
              <div class="list">
                <h3><div class="tit">{{ item.gsmc }}</div></h3>
                <div class="info">
                  <span><i class="iconfont iconlianxiren" />{{ item.lxrmc }}</span>
                  <span><i class="iconfont iconshouji" />{{ item.lxrdh }}</span>
                  <span><i class="iconfont iconyoujian" />{{ item.email }}</span>
                  <span><i class="iconfont iconzhengshubg" />{{ item.shtyxydm }}</span>
                </div>
              </div>
              <template #right>
                <van-button square text="删除" type="danger" class="delete-button" @click="deleteGys(item.jgbh)" />
              </template>
            </van-swipe-cell>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>商品信息</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <div class="goodscart-item">

            <div v-for="(item,index) of spxxlistshow1" :key="index" class="goodsshow-loop">
              <!-- <van-swipe-cell> -->
              <div class="goodsshow-list">
                <div class="goodsshow-c">
                  <div class="goodsshow-img" @click="imgPreview(item.imgSrc?item.imgSrc:defaultImg)">
                    <img :src="item.imgSrc" :onerror="defaultImg">
                  </div>
                  <div class="goodsshow-c-info">
                    <div class="goodsshow-tit" @click="toProductDetail(item.id,item.xmmc)">{{ item.xmmc }}</div>
                    <!-- <div class="goodsshow-tag">
                        <van-tag plain type="primary">{{ item.sourceName }}</van-tag>
                      </div> -->
                    <div class="goodsshow-info">
                      <div class="price">{{ item.dj }}元</div>
                      <div class="num"><van-stepper :key="key" v-model="item.cgsl" min="1" max="99999" integer @change="calPrice(item.cgsl,item.id,index)" /></div>
                    </div>
                  </div>
                </div>
              </div>
              <!-- <template slot="right">
                  <van-button square type="danger" text="删除" @click="deleteCart(item.id)" />
                </template>
              </van-swipe-cell> -->
            </div>
          </div>
        </div>
      </div>

    </div>

    <!-- 竞价时间弹窗 begin -->
    <!-- <van-calendar v-model="show" color="#1890FF" value="" @confirm="onConfirm" /> -->
    <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
      <van-datetime-picker
        v-model="date"
        type="datetime"
        title="选择询价截止时间"
        :show-toolbar="true"
        :min-date="minDate"
        :formatter="formatter"
        @confirm="onConfirm"
        @cancel="cancel()"
      />
    </van-popup>
    <!-- 竞价时间弹窗 end -->
    <div class="common-space" />
    <!-- 底部按钮 begin -->
    <div class="foot-btn fixed">
      <ul>
        <li><van-button type="info" round size="large" @click="gobidsuccess('1')">保存</van-button></li>
      </ul>
    </div>
    <!-- 底部按钮 end -->

    <!-- 选择人员弹出层 beign -->
    <van-popup v-model="showGys" closeable position="bottom" :style="{ height: '100%' }">
      <div class="common-popup">
        <div class="popup-title">选择供应商</div>
        <div class="content">

          <van-sticky class="van-sticky-approve">
            <div class="close-button">
              <i class="iconfont iconguanbi" @click="showGys = false" />
            </div>
            <van-search v-model="gysmc" placeholder="请输入供应商名称" show-action><div slot="action" @click="onSearch">搜索</div></van-search>
          </van-sticky>
          <common-list
            ref="ggcommonlist"
            class="common-list"
            :post-url="list.postUrl"
            :req-data="list.reqData"
            :data-name="list.dataName"
            @returnData="getPrizeLog($event)"
          >
            <div v-for="(item,index) of GysList" :key="index" :class="checkIds.indexOf(item.jgbh)!==-1?'select-renyuan active':'select-renyuan'" @click="checkGys(index)">
              <div class="renyuan-l">
                <i class="iconfont iconxuanzhong" />
              </div>
              <div class="renyuan-m">
                <div class="name">{{ item.gsmc }}</div>
                <div class="info"><span>联系人：{{ item.lxrmc }}</span><span>联系电话：{{ item.lxrdh }}</span></div>
              </div>
            </div>
          </common-list>
        </div>
      </div>
      <div class="common-space" />
      <div class="common-space" />
      <div class="common-space" />
      <div class="common-space" />
      <!-- 底部按钮 begin -->
      <div class="foot-btn fixed">
        <ul>
          <li><van-button type="info" round size="large" @click="close">确定</van-button></li>
        </ul>
      </div>
      <!-- 底部按钮 end -->
    </van-popup>
    <!-- 选择人员弹出层 end -->

  </div>
</template>

<script land="script" scoped>
import headerBar from '@/components/header/headerBar'
import commonList from '@/components/list/commonList1.vue'
import { getProductInfo, zgrwgysList, getDsxjxmxq, saveDsxjXmxx } from '@/store/module.js'
import XyUploadPlus from '@/components/upload/uploadPlus'
import { ImagePreview } from 'vant'
// import { pageRouter } from '@/common/js/mixin.js'
export default {
  name: 'XjMemberEdit',
  components: {
    headerBar,
    commonList,
    XyUploadPlus
  },
  // mixins: [pageRouter],
  data() {
    return {
      key: 1,
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      filegroup: '',
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      xypage: {
        xyCurrentPage: 1, // 当前页
        xyPageSize: 500, // 当前页面展示个数
        xyPageTotal: 0 // 总条数
      },
      AllGysList: [], // 所有可选择供应商列表
      checkIds: [],
      gysmc: '',
      GysList: [], // 搜索后的可选择供应商列表
      spxxlistshow: [],
      spxxlistshow1: [],
      xmxh: '',
      fileChangeInfo: {},
      xzGysList: [], // 选择供应商列表
      spxxlist: [], // 商品信息列表
      num: '1',
      message1: '',
      message2: '',
      message3: '',
      message4: '',
      message5: '',
      message6: '',
      message7: '',
      message8: '',
      message9: '',
      message10: '',
      bidform: { // 页面填写的数据
        czlx: '',
        fbsj: '',
        xmmc: '',
        gysxzfs: '',
        lxrid: '',
        lxr: '',
        lxdh: '',
        email: '',
        qpj: '',
        gyszgyq: '',
        bz: '',
        jjsj: '',
        jjkssj: '',
        jjjssj: '',
        sfbjcs: '',
        bjcs: '',
        sfysjj: '',
        ysfptj: '',
        zdycsj: '',
        sfxzyscs: '',
        sfxzbjcs: '',
        ysfpcs: '',
        djfs: '',
        djslorbfb: '',
        gysxxList: [],
        spxxList: [],
        fj: '',
        spImage: '',
        swyq: '',
        jjgz1: '',
        jjgz2: '',
        yscs: '',
        sfrxyscs: '',
        sfrxysjj: '',
        cjbcsz: '',
        jdsl: ''
      },
      headerData: {
        title: '询价修改页面',
        leftUrl: 'back',
        leftText: ' '
      },
      radio: '1',
      radio1: '3',
      radio2: '5',
      radio3: '7',
      radio4: '9',
      value: '1',
      minDate: new Date(),
      date: new Date(),
      show: false,
      showGys: false,
      imageList: {
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      }
    }
  },
  computed: {

  },
  created() {
    this.xmxh = this.$route.params.xmxh
    this.getXjxx()
  },

  methods: {
    // 选项格式化函数
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } else if (type === 'month') {
        return `${value}月`
      } else if (type === 'day') {
        return `${value}日`
      } else if (type === 'hour') {
        return `${value}时`
      } else if (type === 'minute') {
        return `${value}分`
      } else if (type === 'second') {
        return `${value}秒`
      }
      return value
    },
    cancel() {
      this.show = false
    },
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    // 上传附件回调函数 name:附件名称，fileChanInfo:附件信息
    getFileChangeInfo(name, fileChangeInfo) {
      this.fileChangeInfo[name] = fileChangeInfo
    },
    init() {
      this.list.postUrl = zgrwgysList
      var category_id = ''
      if (this.gysmc === '') {
        if (this.spxxlist && this.spxxlist[0] && this.spxxlist[0].productList) {
          category_id = this.spxxlist[0].productList[0].category_id
        }
      }
      this.list.reqData = {
        pmbh: category_id,
        yxshtyxydm: '',
        pageno: this.xypage.xyCurrentPage,
        pagerow: this.xypage.xyPageSize,
        gysmc: this.gysmc,
        shtyxydm: '',
        cgml: ''
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无更多订单'
    },
    getPrizeLog(data) {
      this.AllGysList = data
      this.GysList = data
    },
    deleteGys(jgbh) {
      for (let i = 0; i < this.xzGysList.length; i++) {
        if (this.xzGysList[i].jgbh === jgbh) {
          this.xzGysList.splice(i, 1)
        }
      }
      for (let i = 0; i < this.checkIds.length; i++) {
        if (this.checkIds[i] === jgbh) {
          this.checkIds.splice(i, 1)
        }
      }
    },
    close() {
      for (let i = 0; i < this.checkIds.length; i++) {
        for (let j = 0; j < this.AllGysList.length; j++) {
          if (this.checkIds[i] === this.AllGysList[j].jgbh) {
            let state = false
            for (let k = 0; k < this.xzGysList.length; k++) {
              if (this.xzGysList[k].jgbh === this.AllGysList[j].jgbh) {
                state = true
              }
            }
            if (state) {
              continue
            } else {
              this.xzGysList.push(this.AllGysList[j])
            }
          }
        }
      }
      this.showGys = false
    },
    onSearch() {
      this.list.reqData.gysmc = this.gysmc
      if (this.list.reqData.gysmc) {
        this.list.reqData.pmbh = ''
      } else {
        if (this.gysmc === '') {
          if (this.spxxlist && this.spxxlist[0].productList) {
            this.list.reqData.pmbh = this.spxxlist[0].productList[0].category_id
          }
        }
      }
      this.$bus.$emit('refList1')
    },
    checkGys(index) {
      let state = false
      let index1 = ''
      for (let i = 0; i < this.checkIds.length; i++) {
        if (this.checkIds[i] === this.GysList[index].jgbh) {
          state = true
          index1 = i
        }
      }
      if (state && index1 !== '') {
        this.checkIds.splice(index1, 1)
      } else {
        this.checkIds.push(this.GysList[index].jgbh)
      }
    },
    toProductDetail(product_id, spmc) {
      this.$store.commit('goods/SET_SPMC', spmc)
      this.$store.commit('goods/SET_SPID', product_id)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    // 删除商品
    deleteCart(id) {
      for (var i = 0; i < this.spxxlist.length; i++) {
        var productList = this.spxxlist[i]
        if (productList.id === id) {
          this.spxxlist.splice(i, 1)
        }
      }
      for (var x = 0; x < this.spxxlistshow1.length; x++) {
        var productList1 = this.spxxlistshow1[x]
        if (productList1.id === id) {
          this.spxxlistshow1.splice(x, 1)
        }
      }
    },
    calPrice(val, id, index) {
      if (this.spxxlistshow1[index].cgsl === '-') {
        this.spxxlistshow1[index].cgsl = 1
        val = 1
      }
      if (val > 99999) {
        this.spxxlistshow1[index].cgsl = 99999
        val = 99999
        this.key++
      }
      for (let i = 0; i < this.spxxlist.length; i++) {
        const item = this.spxxlist[i]
        if (item.id === id) {
          item.cgsl = val
        }
      }
    },
    goGrzx() {
      this.$store.commit('mall/SET_ISACTIVE', 3)
      this.$router.push('MallMine')
    },
    zhTime(time) {
      const year = time.getFullYear()
      const month = (time.getMonth() + 1) < 10 ? ('0' + (time.getMonth() + 1)) : (time.getMonth() + 1)
      const day = time.getDate() < 10 ? ('0' + time.getDate()) : time.getDate()
      const hour = time.getHours() < 10 ? ('0' + time.getHours()) : time.getHours()
      const minute = time.getMinutes() < 10 ? ('0' + time.getMinutes()) : time.getMinutes()
      const sj = year + '' + month + '' + day + '' + hour + '' + minute
      return sj
    },
    // 提交当前页面输入数据
    gobidsuccess(czlx) {
      var temp = ''
      for (let i = 0; i < 4; i++) {
        temp += this.check(i)
      }
      if (temp !== '') {
        this.$toast.fail('请完善页面信息')
        return
      }
      if (this.bidform.gysxzfs === '2' && this.xzGysList.length < 3) {
        this.$toast.fail('至少选择三家供应商！')
        return
      }
      const arr = []
      for (let k = 0; k < this.xzGysList.length; k++) {
        const item = {}
        item.gysid = this.xzGysList[k].jgbh
        item.ztdm = this.xzGysList[k].shtyxydm
        item.gysmc = this.xzGysList[k].gsmc
        item.lxr = this.xzGysList[k].lxrmc
        item.lxfs = this.xzGysList[k].lxrdh
        item.email = this.xzGysList[k].email
        item.zbjgid = ''
        item.sfzb = ''
        item.zbje = ''
        arr.push(item)
      }
      saveDsxjXmxx.data = {
        xjxmid: this.xmxh,
        czlx: czlx,
        xmmc: this.bidform.xmmc,
        gysxzfs: this.bidform.gysxzfs,
        lxrid: this.$store.state.mh.mhUserInfo.id,
        lxrmc: this.bidform.lxr,
        lxdh: this.bidform.lxdh,
        email: this.bidform.email,
        xjjzsj: this.bidform.xjjzsj,
        gysxxList: this.bidform.gysxzfs === '2' ? arr : [],
        spxxList: this.spxxlist,
        bz: this.bidform.bz,
        fileChangeInfo: this.fileChangeInfo
      }
      this.asyncApiModule(saveDsxjXmxx).then(res => {
        this.$toast('修改成功！')
        this.$router.back()
      }).catch(error => {
        console.log('error===', error)
      })
    },
    toStrTime(str) {
      return str.slice(0, 4) + '/' + str.slice(4, 6) + '/' + str.slice(6, 8)
    },
    // 转换竞价持续时间
    getTime() {
      if (this.bidform.jjsj1) {
        const second = (this.bidform.jjsj1[1] - this.bidform.jjsj1[0]) / 1000
        const day = Math.floor(second / 60 / 60 / 24)
        const houre = Math.floor(second / 60 / 60 - day * 24)
        const minute = Math.floor(second / 60 - (day * 24 + houre) * 60)
        const seconds = second % 60
        return day + '天' + houre + '时' + minute + '分' + seconds + '秒'
      }
    },
    formatDate(date) {
      const year = date.getFullYear()
      const month = (date.getMonth() + 1) < 10 ? ('0' + (date.getMonth() + 1)) : date.getMonth()
      const day = date.getDate() < 10 ? ('0' + date.getDate()) : date.getDate()
      const houre = date.getHours() < 10 ? ('0' + date.getHours()) : date.getHours()
      const minute = date.getMinutes() < 10 ? ('0' + date.getMinutes()) : date.getMinutes()
      return year + '-' + month + '-' + day + ' ' + houre + ':' + minute
      // return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()} ${date.getHours()}:${date.getMinutes()}`
    },
    strToTime(str) {
      return new Date(str.slice(0, 4), str.slice(4, 6), str.slice(6, 8), str.slice(8, 10), str.slice(10, 12), str.slice(12, 14))
    },
    getXjxx() {
      getDsxjxmxq.data = { xmxh: this.xmxh }
      this.asyncApiModule(getDsxjxmxq).then(res => {
        this.spxxlist = res.spxxList
        res.xjjzsj = res.xjjzsj + ''
        var date1 = res.xjjzsj.slice(0, 4) + '-' + res.xjjzsj.slice(4, 6) + '-' + res.xjjzsj.slice(6, 8) + ' ' + res.xjjzsj.slice(8, 10) + ':' + res.xjjzsj.slice(10, 12)
        if (res.gysxzfs === 2) {
          this.xzGysList = res.gysxxList
          this.bidform.gysxzfs = '2'
        }
        if (res.gysxzfs === 1) {
          this.bidform.gysxzfs = '1'
        }
        const arr = []
        for (let k = 0; k < res.gysxxList.length; k++) {
          const item = {}
          item.jgbh = res.gysxxList[k].gysid
          item.shtyxydm = res.gysxxList[k].ztdm
          item.gsmc = res.gysxxList[k].gysmc
          item.lxrmc = res.gysxxList[k].lxr
          item.lxrdh = res.gysxxList[k].lxfs
          item.email = res.gysxxList[k].email
          item.zbjgid = ''
          item.sfzb = ''
          item.zbje = ''
          arr.push(item)
        }
        this.xzGysList = arr
        this.bidform.lxr = res.lxrmc
        this.bidform.lxdh = res.lxdh
        this.bidform.xmmc = res.xmmc
        this.bidform.jzsj = date1
        this.bidform.xjjzsj = res.xjjzsj
        this.bidform.bz = res.bz
        this.bidform.email = res.email
        this.filegroup = res.fj
        for (let i = 0; i < this.spxxlist.length; i++) {
          this.spxxlistshow.push(JSON.parse(JSON.stringify(this.spxxlist[i])))
        }
        this.getSpxx()
        this.init()
      }).catch(error => {
        console.log(error)
      })
    },
    getSpxx() {
      for (let i = 0; i < this.spxxlistshow.length; i++) {
        const spxx = this.spxxlistshow[i]
        getProductInfo.data = { e_product_id: spxx.id }
        this.asyncApiModule(getProductInfo).then(res => {
          spxx.catename = res.catename
          spxx.imgSrc = res.e_product.picture
          spxx.status = res.e_product.status
          spxx.cart_id = res.cart_id
          this.spxxlistshow1.push(spxx)
        })
      }
    },
    check(val) {
      if (val === 0) {
        if (this.bidform.xmmc.length === 0) {
          this.message1 = '请输入项目名称'
          return this.message1
        } else {
          this.message1 = ''
          return ''
        }
      }
      if (val === 2) {
        if (this.bidform.gysxzfs.length === 0) {
          this.message3 = '请选择供应商征集方式'
          return this.message3
        } else {
          this.message3 = ''
          return ''
        }
      }
      if (val === 3) {
        if (this.bidform.email.length === 0) {
          this.message4 = '请填写邮箱'
          return this.message4
        } else if (/^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/.test(this.bidform.email) === false) {
          this.message4 = '邮箱格式不正确'
          return this.message4
        } else {
          this.message4 = ''
          return ''
        }
      }
      if (val === 1) {
        if (this.bidform.jzsj.length === 0) {
          this.message2 = '请选择询价截止时间'
          return this.message2
        } else {
          this.message2 = ''
          return ''
        }
      }
    },
    showPopup() {
      this.show = true
    },
    showXmztPopup() {
      this.show1 = true
    },
    showcalendar() {
      this.show2 = true
    },
    showGdczPopup() {
      this.show3 = true
    },
    showGysPopup() {
      this.checkIds = []
      this.gysmc = ''
      this.onSearch()
      this.showGys = true
    },
    onConfirm() {
      this.show = false
      this.bidform.xjjzsj = this.zhTime(this.date)
      this.bidform.jzsj = this.formatDate(this.date)
      this.check(1)
    }
  }
}
